input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

label {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 20px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 100px;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: #fff;
  border-radius: 10px;
  margin: 0 auto;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + label {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + label:after {
  left: 39px;
  transform: translateX(-100%);
}

