@import url(./variables.css);
h1{
    color: var(--black-first-color);
    font-family: DM Sans;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    /* 118.75% */
    letter-spacing: -1.28px;
    margin-bottom: 40px;
}
h1 span{
    color: var(--violet);
    font-family: DM Sans;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    letter-spacing: -1.28px;
}
.js-container-categories{
    display: block;
    border: solid 1px red;
    margin-bottom: 40px;
}

.img-book{
    width: 335px;
}
.js-item-book:nth-last-child(-n+4){
    display: none; 
}
.js-title-book{
    padding-top: 14px;
}

.js-name-book{
    color: #111;
    font-family: DM Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 18px;
    /* 112.5% */
    letter-spacing: -0.64px;
    text-transform: uppercase;
}
.js-contributor-book{
   padding-top: 4px; 
   color: var(--gray);
   font-size: 12px;
   font-style: italic;
   font-weight: 400;
   line-height: 14px;
   /* 116.667% */
   letter-spacing: -0.48px;
   padding-bottom: 8px;
}

.js-btn-category{
    display: block;
    margin-left: auto;
    padding: 14px 28px;
    align-items: flex-start;
    gap: 10px;
    border-radius: 40px;
    border: 2px solid var(--violet);
    color: var(--black-first-color);
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 18px;
        /* 128.571% */
    letter-spacing: -0.14px;
    text-transform: uppercase;
}
.js-name-category{
    color: var(--gray);
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
        /* 128.571% */
    letter-spacing: 0.42px;
    text-transform: uppercase;
}

@media screen and (min-width: 768px) {

    .js-item-book:nth-last-child(-n+2) {
            display: block;
        }
}

@media screen and (min-width: 1280px) {
.js-item-book:nth-last-child(-n+4) {
        display: block;
    }
}
